* {
    box-sizing: border-box;
    margin:0;
    padding:0;
}

body {
    background-color: white;
}

header {
    background-color: white;
}
li,a,button {
    font-weight: 500;
    font-size:0.89em;
    text-decoration: none;
    color: #2b2c2c;
}
.red-line{
    grid-column: 3 /6;
    grid-row: 5;
    border-bottom: 25px solid #f9423a;
    align-self: start;
    justify-content: center;
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:30px 1%;
}

.logo {
    cursor: pointer;
    margin:0px 0px;
    padding: auto;
    width:55px;
    height: 55px;
    background: transparent;
    background-color: white;
}

.nav__links {
    list-style: none;
}

.nav__links li {
    display: inline-block;
    padding: 0px 20px;
}

.nav__links li a {
    transition:all 0.3s ease 0s;
}

.nav__links li a:hover {
    color: #0088ae;
}

.main-content-wrapper {
    display: flex;
    width:60vw;
    font-family: 'Coda Caption', sans-serif;
    font-size:2em;
    padding: 0.4em;
    margin: auto 30%;
    min-height: 90vh;
    justify-content: center;
    align-items: center;

    border-radius: 20px;
}
.lolo {
    width:100vw;
    display: flex;
    flex-direction: row;
    margin-top: 30vh;
    margin-bottom: 30vh;
    background-color: beige;
}
@media screen and (max-width:700px) {
    .lolo {
        flex-direction: column;
    }
    .lolo > p {
        margin-right: 10px;
        justify-self: start;
        padding: 0.4em;
    }
}
@media screen and (min-width:700px) {
.lolo > p {
    order:2;
    max-width: 400px;
    margin-left: 160px;
    margin-top: 60px;
    font-size: .9em;
    line-height: 1.6;
    padding: 5px;
}
}
.main-content-wrapper-2 {
    display: flex;
    background-color: rgb(255, 255, 255);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    margin-top:10%;
    border-radius: 5px;
    font-size: 1.2em;
    height: 60vh;
    align-items: center;
    line-height: 2;
    padding: 2em 2em;
}
.main-dix {
    background-color: gray;
    margin: 12% 23%;
    width:50%;
    padding:30px 20px;
    height: 350px;
    /* margin:40px 40px; */
    text-align: center;
    font-weight: 300;
    border-radius: 10px;
    font-size:.9em
}

.footer {
   text-align: center;
   display: flex;
   justify-content: space-around;
   background-color: rgb(75, 80, 75);
   flex-shrink: 0;
   padding: 40px;
   color: rgb(199, 195, 197);
   font-size: 0.6em;
}

.footer > span {
    background-color: rgb(75, 80, 75);
}

.wrapper {
    display: grid;
    max-width: 1200px;
    margin: auto ;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.item {
    display: flex;
    background-color: rgb(130, 130, 233);
    border-radius: 5px;
    padding: 1rem;
    max-width: 270px;
   height: 300px;
   justify-content: flex-end;
   align-items: flex-end;
    
}
.new{
   
    margin:auto auto;
   
    background-color: chartreuse;

}
.cat {
    position: relative;
    top:50px;
    right: 10px;
    font-style: italic;
}
