.content-wrapper {
    display: block;
    background-color: rgb(221, 218, 217);
    margin-top: 15px;
    width:90%;
    border-radius: 10px;
    margin:25px auto;
    padding: 30px;

}

.til {
    font-family: 'Roboto Slab';
    font-weight: bolder;
    font-size: 0.9em;
}
.para {
    margin:10px auto;
    font-weight: 400;
    font-size: .8em;
    line-height: 1.5;
    height: auto;
    min-height: 25vh;
}
.tag {
    display: inline-block;
    background-color: rgb(255, 255, 255);
    font-family: 'Roboto Slab';
    padding: 6px 10px;
    border-radius: 10px;
    margin-left: 5px;
    margin-bottom: 5px;
    font-size: .8em;
    font-weight: lighter;
}
.header-wrapper {
    display: grid;
    margin:50px 10px;
    width: 40vw;
    font-size: .8em;
    grid-template-columns: auto 1fr 1fr 1fr 0.5fr;
    grid-template-rows: auto 1fr 1fr auto auto;
    grid-gap: 1rem;
    transform: rotate(0deg);
}

.header-wrapper h1:nth-child(1) {
    grid-column:1 / 5 ;
    text-align: right;
}
.header-wrapper h1:nth-child(2) {
    color: #f9423a;
    writing-mode: vertical-rl;
    grid-column: 3 / 4;
    grid-row: 2 / 5;
    justify-self: start;
    margin-top: -10px;
    margin-right: -10px;
}
.header-wrapper h1:nth-child(3) {
    color: #f9423a;
    grid-column: 4 / 6;
    grid-row: 2;
    align-self: flex-start;
}
.header-wrapper h1:nth-child(4) {

    grid-column: 4;
    grid-row: 2 / 5;
    writing-mode:vertical-rl;
    align-self: center;
    margin-right: -10px;
}
.header-wrapper h1:nth-child(5) {
    grid-column: 2;
    grid-row: 3;
}
.header-wrapper h1:nth-child(6) {
    color: #f9423a;
    grid-column: 5 /6;
    grid-row: 4;

}
.references {
    grid-column: 2 / 3;
    grid-row: 4 / span 2;
    font-weight: 700;
    line-height: 2;
    align-self: end;
    justify-self: right;
    
}

.references > ul {
    list-style: none;
}